.center-block {
    display: flex;
    justify-content: center;
    align-items: center;

}

.menuButton {
    padding: 20px;
    text-align: center;
}
.menuButton button {
    width: 150px;
    height: 50px;
    background-color: orange;
    border: none;
    border-radius: 10px;
}


.category-buttons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-around;
    gap:50px;
    margin: 100px;


}

.category-button {
    background: white;
    border: none;
    width: 200px;
    height: 200px;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
    border-radius: 10px;
}
.buttonItem {
    margin-top: 30px;
    display: flex;
    justify-content: space-around;

}

.buttonAdd {
    background-color: green;
    color: white;
    cursor: pointer;
    border: none;
    width: 15%;
    height: 60px;
    border-radius: 10px;

}
.buttonClose {
    background-color: red;
    color: white;
    cursor: pointer;
    border: none;
    width: 15%;
    border-radius: 10px;
}
ul {
    list-style: none;
    padding: 0;
}

li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    padding: 30px;
}

.category-button img {
    width: 200px;
    height: 200px;
}
@media (max-width: 768px) {
    .center-block {
        display: block;
        text-align: center;
        align-items: center;
    }

    .menuButton {
        padding: 10px;
        text-align: center;
    }

    .menuButton button {
        width: 100px;
        height: 40px;
        background-color: orange;
        border: none;
        border-radius: 10px;
        font-size: 14px;
    }

    .category-buttons {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 20px;
        margin: 20px;
    }

    .category-button {
        background: white;
        border: none;
        width: 100%;
        height: 120px;
        font-size: 14px;
        cursor: pointer;
        text-align: center;
        border-radius: 10px;
    }

    .buttonItem {
        margin-top: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .buttonAdd, .buttonClose {
        background-color: green;
        color: white;
        cursor: pointer;
        border: none;
        width: 30%;
        height: 40px;
        border-radius: 10px;
        font-size: 14px;
    }

    ul {
        list-style: none;
        padding: 0;
    }

    li {
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #ccc;
        padding: 10px;
    }

    .category-button img {
        width: 100px;
        max-height: 100px;
        object-fit: cover;
        border-radius: 10px;
        align-items: center;
    }
}